<template>
    <div class="chart-buttons">
        <button class="chart-button" @click="selectPeriod(1)" :class="selectedValue==1 ? 'selected' : ''">1</button>
        <button class="chart-button" @click="selectPeriod(3)" :class="selectedValue==3 ? 'selected' : ''">3</button>
        <button class="chart-button" @click="selectPeriod(6)" :class="selectedValue==6 ? 'selected' : ''">6</button>
        <button class="chart-button" @click="selectPeriod('YTD')" :class="selectedValue=='YTD' ? 'selected' : ''">YTD</button>
        <button class="chart-button" @click="selectPeriod(12)" :class="selectedValue==12 ? 'selected' : ''">1Y</button>
        <button class="chart-button" @click="selectPeriod('all')" :class="selectedValue=='all' ? 'selected' : ''">All</button>
    </div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'

export default defineComponent({
    setup() {
        
    },
    data: ()=> ({
        selectedValue: 1 // 1, 3, 6, 12, 'YTD', 'all'
    }),
    methods: {
        selectPeriod(value) {
        this.selectedValue = value
        let startTime = null
        let endTime = null
        if (value == 'all') {
            // select all time
            // startTime = null, endTime = null
        } else if (value == 'YTD') {
            // select a period from first day of current year to current date
            const startDate = new Date(new Date().getFullYear(), 0, 1);
            const endDate = new Date()
            startTime = startDate.getTime()
            endTime = endDate.getTime()
        } else {
            // select a period 1,3,6 or 12 months before the current date
            const endDate = new Date()
            const startDate = new Date()
            this.$utils.addMonths(startDate, -value)
            startTime = startDate.getTime()
            endTime = endDate.getTime()
        }
            this.$emit('selectPeriod', startTime, endTime)
        },
    }   
})
</script>

